import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { AppRoutingModule } from './app-routing.module';
import { AppComponent } from './app.component';
import { FormsModule } from '@angular/forms';
// import { HttpModule } from '@angular/http';
import { HttpClientModule } from '@angular/common/http';
// import { Angular2CsvModule } from 'angular2-csv';
import { CookieService } from 'ngx-cookie-service';
import { RouterModule, Routes } from '@angular/router';
import {NgbModule} from '@ng-bootstrap/ng-bootstrap';
import {NgbPaginationModule, NgbAlertModule} from '@ng-bootstrap/ng-bootstrap';
import {AutocompleteLibModule} from 'angular-ng-autocomplete';

import { FormModelComponent } from './form-model/form-model.component';
import { LoginComponent } from './login/login.component';
import { ProfileComponent } from './profile/profile.component';
import { LogoutComponent } from './logout/logout.component';
import { ChangePasswordComponent } from './change-password/change-password.component';
import { WorkGroupComponent } from './work-group/work-group.component';
import { DailingComponent } from './dailing/dailing.component';
import { AgentStatusComponent } from './agent-status/agent-status.component';
import { DashboardComponent } from './dashboard/dashboard.component';
import { CallToolComponent } from './call-tool/call-tool.component';
import { WrapupComponent } from './wrapup/wrapup.component';
import { CallTransferComponent } from './call-transfer/call-transfer.component';
import { MainScreenComponent } from './main-screen/main-screen.component';
import { InteractionDetailsComponent } from './interaction-details/interaction-details.component';
import { Interaction2Component, SortableHeader } from './interaction2/interaction2.component';
import { SubscriptionsService} from './services/subscriptions.service';

import { MustMatchDirective } from './change-password/must-match.directive'
import { NgbdSortableHeader } from './interaction-details/sortable.directive';

@NgModule({
  declarations: [
    AppComponent,
    FormModelComponent,
    LoginComponent,
    ProfileComponent,
    LogoutComponent,
    ChangePasswordComponent,
    WorkGroupComponent,
    DailingComponent,
    AgentStatusComponent,
    DashboardComponent,
    CallToolComponent,
    WrapupComponent,
    CallTransferComponent,
    MainScreenComponent,
    InteractionDetailsComponent,
    Interaction2Component,
    MustMatchDirective,
    NgbdSortableHeader,
    SortableHeader
  ],
  imports: [
    BrowserModule,
    FormsModule,
    AppRoutingModule,
    // HttpModule,
    HttpClientModule,
    // Angular2CsvModule,
    RouterModule,
    NgbModule,
    NgbPaginationModule,
    NgbAlertModule,
    AutocompleteLibModule
  ],
  providers: [ CookieService, SubscriptionsService
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }
