import { Component, OnInit, QueryList, ViewChildren } from '@angular/core';
import {DecimalPipe} from '@angular/common';
import {Observable} from 'rxjs';

import {Interaction} from './interaction';
import {InteracationServices} from './interaction.services';
import {NgbdSortableHeader, SortEvent} from './sortable.directive';

@Component({
  selector: 'app-interaction-details',
  templateUrl: './interaction-details.component.html',
  styleUrls: ['./interaction-details.component.scss'],
  providers: [InteracationServices, DecimalPipe]
})
export class InteractionDetailsComponent implements OnInit {

  countries$: Observable<Interaction[]>;
  total$: Observable<number>;

  @ViewChildren(NgbdSortableHeader) headers: QueryList<NgbdSortableHeader>;

  constructor(public service: InteracationServices) {
 
    this.countries$ = service.countries$;
    this.total$ = service.total$;
    // console.log(this.countries$);
    // console.log(this.countries$['value']);
  }

  onSort({column, direction}: SortEvent) {
    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    this.service.sortColumn = column;
    this.service.sortDirection = direction;
  }

  ngOnInit() {
  }

}
