import { Component, OnInit } from '@angular/core';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';
import { $ } from 'protractor';

@Component({
  selector: 'app-dailing',
  templateUrl: './dailing.component.html',
  styleUrls: ['./dailing.component.scss']
})
export class DailingComponent implements OnInit {
  model: any = {};
  number: '';
  closeResult: string;
  selectedOption: string;
  printedOption: string;
  options = [
    { name: "CIC1 Workgroup", value: "CIC1 Workgroup"},
    { name: "CIC1 Workgroup2", value: "CIC1 Workgroup2"},
  ]
  private _sessionId: string;
  private _tocken: string;
  private _cookie: string;

  constructor(private modalService: NgbModal, private http: HttpClient, private cookieService: CookieService) {
    this._sessionId =  cookieService.get('sessionId');
    this._tocken = cookieService.get('csrfToken');
    this._cookie = cookieService.get('Set-Cookie');
  }

  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
      this.closeResult = `Closed with: ${result}`;
    }, (reason) => {
      this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
    });
  }

  private getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: ${reason}`;
    }
  }

  makecall(f) {
    console.log(f.value);
    console.log(this._cookie);
    this.cookieService.set( 'number', f.value['number'] );
    this.printedOption = this.selectedOption;
    let url= "http://cic1.cexfinance.com:8018/icws/"+this._sessionId+"/interactions";
    const headers = new Headers();
    const options = { 
                      headers: new HttpHeaders({'ININ-ICWS-CSRF-Token': this._tocken}),
                      withCredentials: true
                    };
    let body = {
                  "__type": "urn:inin.com:interactions:createCallParameters",
                  "callMadeStage": 1,
                  "additionalAttributes":{},
                  "target":f.value['number'],
                  "workgroup":f.value['workgroup']
                };

    return this.http.post(url, body, options).subscribe(
          data2 => {
                      console.log(data2);
                      var interactionId = data2['interactionId'];
                      this.cookieService.set( 'interactionId', interactionId );
                    },
          err => { 
                    console.log(err);
                  }
        );
    }

  ngOnInit() {

              }
}
