import { Component, OnInit } from '@angular/core';
import { Observable, Subject } from 'rxjs';
// import {  Http,  Response,  Headers,  RequestOptions  } from '@angular/http';  
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { map } from 'rxjs/operators';

@Component({
  selector: 'app-form-model',
  templateUrl: './form-model.component.html',
  styleUrls: ['./form-model.component.sass']
})
export class FormModelComponent implements OnInit {
  username: string = '';
  password: string = '';

    constructor(private http: HttpClient) {}
    dosubmit(f) {
      let server_info_url= '192.168.1.213:80/icws/connection/server-info';
      let url= 'http://192.168.1.211:8018/icws/connection';
      const headers = new Headers();
      const options = { headers: new HttpHeaders({ 'Content-Type': 'application/json', 'Accept-Language':'any'}) };
      let body = {
        "__type": "urn:inin.com:connection:icAuthConnectionRequestSettings",
        "applicationName":"ICWS",
        "userID":f.value['username'],
        "password":f.value['password']
      };

      return this.http.post(url, body, options).subscribe(
            data => {
                      if (data['status'] == 'success') {
                          window.location.href = 'https://www.google.com/';
                      } else {
                        alert('Please check your details');
                      }
                    },
            err => { console.log(err); }
          );
    }
    ngOnInit() {

  }

}
