import { Component, OnInit } from '@angular/core';
import { CookieService } from 'ngx-cookie-service';
import { HttpClient, HttpHeaders } from '@angular/common/http';

@Component({
  selector: 'app-logout',
  templateUrl: './logout.component.html',
  styleUrls: ['./logout.component.scss']
})
export class LogoutComponent implements OnInit {
  
  private _sessionId: string;
  private _tocken: string;

  constructor(private http: HttpClient, private cookieService: CookieService) { 
    this._sessionId =  cookieService.get('sessionId');
    this._tocken = cookieService.get('csrfToken');
  }
logout() {
  let url= "http://192.168.1.211:8018/icws/"+this._sessionId+"/connection";
  const headers = new Headers();
  const options = { 
                    headers: new HttpHeaders(
                          { 'ININ-ICWS-CSRF-Token': this._tocken}
                    ),
                    withCredentials: true
                  };

  return this.http.delete(url, options).subscribe(
      data => { 
                console.log(data); 

              },
      err => { 
                console.log(err);
              }
  );
}

  ngOnInit() {
  }

}
