import { Component, OnInit } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { CookieService } from 'ngx-cookie-service';
import {NgbModal, ModalDismissReasons} from '@ng-bootstrap/ng-bootstrap';

@Component({
  selector: 'app-call-tool',
  templateUrl: './call-tool.component.html',
  styleUrls: ['./call-tool.component.scss']
})
export class CallToolComponent implements OnInit {

  val: string = ''; 
  closeResult: string;
  private _sessionId: string;
  private _tocken: string;
  private _interactionId: string;
  private _number: string;
  transfer = [
    
  ]

  constructor(private http: HttpClient, private cookieService: CookieService, private modalService: NgbModal) {
    this._sessionId =  cookieService.get('sessionId');
    this._interactionId =  cookieService.get('interactionId');
    this._tocken = cookieService.get('csrfToken');
    this._number = cookieService.get('number');
  }
    
  open(content) {
    this.modalService.open(content, {ariaLabelledBy: 'modal-basic-title'}).result.then((result) => {
        this.closeResult = `Closed with: ${result}`;
      }, (reason) => {
        this.closeResult = `Dismissed ${this.getDismissReason(reason)}`;
      });
    }

  private getDismissReason(reason: any): string {
      if (reason === ModalDismissReasons.ESC) {
        return 'by pressing ESC';
      } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
        return 'by clicking on a backdrop';
      } else {
        return  `with: ${reason}`;
      }
    }

  pickup() {
    console.log('Call pickup');
    let url= "http://192.168.1.211:8018/icws/"+this._sessionId+"/interactions/"+this._interactionId+"/pickup";
    const headers = new Headers();
    const options = { 
                      headers: new HttpHeaders({'ININ-ICWS-CSRF-Token': this._tocken}),
                      withCredentials: true
                    };

    return this.http.post(url, options).subscribe(
          pickup => {
                      console.log(pickup);
                    },
          err => {
                    console.log(err);
                  }
        );
    }
  
  holdcall() {
    console.log('Call on hold');
    let url= "http://192.168.1.211:8018/icws/"+this._sessionId+"/interactions/"+this._interactionId+"/hold";
    const headers = new Headers();
    const options = { 
                      headers: new HttpHeaders({'ININ-ICWS-CSRF-Token': this._tocken}),
                      withCredentials: true
                    };
    let body = {
                  "on":"false"
                };

    return this.http.post(url, body, options).subscribe(
          hold => {
                      console.log(hold);
                    },
          err => {
                    console.log(err);
                  }
        );
    }
  
  mutecall() {
    let url= "http://192.168.1.211:8018/icws/"+this._sessionId+"/interactions/"+this._interactionId+"/mute";
    const headers = new Headers();
    const options = { 
                      headers: new HttpHeaders({'ININ-ICWS-CSRF-Token': this._tocken}),
                      withCredentials: true
                    };
    let body = {
                  "on":"false"
                }

    return this.http.post(url, body, options).subscribe(
          mute => {
                      console.log(mute);
                    },
          err => {
                    console.log(err);
                  }
        );
    }
  
  transfercall(f) {
    let url= "http://192.168.1.211:8018/icws/"+this._sessionId+"/interactions/"+this._interactionId+"/blind-transfer";
    const headers = new Headers();
    const options = { 
                      headers: new HttpHeaders({'ININ-ICWS-CSRF-Token': this._tocken}),
                      withCredentials: true
                    };
    let body = {
        "target": 2002
      }
 
    return this.http.post(url, body, options).subscribe(
          transfer => {
                      console.log(transfer);
                    },
          err => {
                    console.log(err);
                  }
        );
    }
  
  disconnectcall() {
    console.log('Call disconnected');
    let url= "http://192.168.1.211:8018/icws/"+this._sessionId+"/interactions/"+this._interactionId+"/disconnect";
    const headers = new Headers();
    const options = { 
                      headers: new HttpHeaders({'ININ-ICWS-CSRF-Token': this._tocken}),
                      withCredentials: true
                    };

    return this.http.post(url, options).subscribe(
          disconnect => {
                      console.log(disconnect);
                    },
          err => {
                    console.log(err);
                  }
        );
    }

  lookupEntries(f) {
    let url= "http://192.168.1.211:8018/icws/"+this._sessionId+"/directories/lookup-entrie";
    const headers = new Headers();
    const options = { 
                      headers: new HttpHeaders({'ININ-ICWS-CSRF-Token': this._tocken}),
                      withCredentials: true
                    };
    let body = {
      "lookupComparisonType": "StartsWith",
      "lookupEntryProperties" : ["displayName","entryID","extension","firstName","lastName"],
      "lookupEntryTypes" : ["User","Workgroup","Station","Stand-alone Phone","Station Group","Attendant Profile"],
      "lookupString" : f.value['transfer'],
      "maxEntries" : 10
      }
 
    return this.http.post(url, body, options).subscribe(
      lookupEntries => {
                      console.log(lookupEntries);
                    },
          err => {
                    console.log(err);
                  }
        );
    }

  ngOnInit() {
  }

}
