import { Component, OnInit, Directive, EventEmitter, Input, Output, QueryList, ViewChildren } from '@angular/core';

interface interactions {
  name: number;
  duration: number;
  state: string;
  queue: string;
  station: number;
  user: string;
  interaction: number;
}

const INTERACTION: interactions[] = [
  {
    name: 9703696072,
    duration: 123123,
    state: 'Disconnected',
    queue: 'string',
    station: 1007,
    user: 'GOPAL',
    interaction: 2341242345235
  },
  {
    name: 9441100072,
    duration: 123123,
    state: 'Disconnected',
    queue: 'string',
    station: 1007,
    user: 'GOPAL',
    interaction: 2341242345235
  }
];

export type SortDirection = 'asc' | 'desc' | '';
const rotate: {[key: string]: SortDirection} = { 'asc': 'desc', 'desc': '', '': 'asc' };
export const compare = (v1, v2) => v1 < v2 ? -1 : v1 > v2 ? 1 : 0;

export interface SortEvent {
  column: string;
  direction: SortDirection;
}

@Directive({
  selector: 'th[sortable]',
  host: {
    '[class.asc]': 'direction === "asc"',
    '[class.desc]': 'direction === "desc"',
    '(click)': 'rotate()'
  }
})
export class SortableHeader {

  @Input() sortable: string;
  @Input() direction: SortDirection = '';
  @Output() sort = new EventEmitter<SortEvent>();

  rotate() {
    this.direction = rotate[this.direction];
    this.sort.emit({column: this.sortable, direction: this.direction});
  }
}

@Component({
  selector: 'app-interaction2',
  templateUrl: './interaction2.component.html',
  styleUrls: ['./interaction2.component.scss']
})
export class Interaction2Component implements OnInit {

  interactions = INTERACTION;

  @ViewChildren(SortableHeader) headers: QueryList<SortableHeader>;

  onSort({column, direction}: SortEvent) {

    // resetting other headers
    this.headers.forEach(header => {
      if (header.sortable !== column) {
        header.direction = '';
      }
    });

    // sorting countries
    if (direction === '') {
      this.interactions = INTERACTION;
    } else {
      this.interactions = [...INTERACTION].sort((a, b) => {
        const res = compare(a[column], b[column]);
        return direction === 'asc' ? res : -res;
      });
    }
  }


  constructor() { }

  ngOnInit() {
  }

}
